.repeatableAddElt {
    border: 1px dashed $border300;
    width: 100%;
    box-shadow: none;
    color: rgb(200 200 200);
    justify-content: center;
    margin-top: 5px;

    .dashicon {
        font-size: 1rem;
        height: auto;
        margin-right: 3px;
        transition: none;
    }

    &:hover {
        color: var(--wp-admin-theme-color);
        border-color: var(--wp-admin-theme-color);
    }
}

.repeatableContainer {
    display: flex;
    flex-direction: column;      
    gap: $o-space-20;

    .repeatableItem {
        position: relative;
        padding-inline-end: 0;
        transition: $o-transition-fast;

        &:hover {
            padding-inline-end: 2.5em;
        }

        .sortableItemInner {

            .repeatableObjectLabel {
                display: flex;
                gap: $o-space-20;
                margin-block-start: 5px;

                .id {
                    font-weight: bolder;
                }

                .props {
                    display: flex;
                    gap: $o-space-20;

                    .key {
                        opacity: 0.5;
                    }
                }
            }
        }

        .drag {
            position: absolute;
            right: 0;
            top: 0;
            padding: 0;
            margin: 0;
            color: $o-text-color;
            opacity: 0;
            box-shadow: none !important;
            transition: $o-transition-fast;
        
            &:hover {
                opacity: 1;
            }

            .dashicon {
                font-size: 1rem;
                height: 1rem;
            }
        }
        &:hover .drag {
            opacity: 0.3;
        }

        .remove {
            position: absolute;
            right: 0;
            top: 2em;
            color: $o-text-color;
            opacity: 0;
            box-shadow: none !important;
            transition: $o-transition-fast;

            &:hover {
                opacity: 1;
            }

            .dashicon {
                font-size: 1rem;
                height: 1rem;
            }
        }
        &:hover .remove {
            opacity: 0.3;
        }
    }
}